body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  background-color: #ffffff;
  color: #011c40;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px; }

h1 {
  flex: 1 0 auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

a {
  text-decoration: none; }

html {
  height: 100%;
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

*,
*::before,
*::after {
  box-sizing: inherit; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.page-wrap {
  max-width: 1240px;
  padding: 0 40px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .page-wrap {
    max-width: 678px;
    padding: 0; } }

@media (max-width: 767px) {
  .page-wrap {
    max-width: auto;
    padding: 0 15px; } }

.container {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.header {
  flex: 1 0 auto;
  padding-top: 35px; }

.header__image::before {
  background: url("../img/intro@1x-desktop.jpg") no-repeat top center;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  width: 100%;
  height: 650px;
  content: "";
  display: block;
  position: absolute; }

@media (max-width: 1023px) {
  .header {
    padding-top: 20px; }
  .header__image::before {
    background: url("../img/intro@1x-tablet.jpg") no-repeat top center;
    width: 100%;
    height: 638px; } }

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.nav__item {
  padding-top: 10px;
  padding-bottom: 10px; }
  .nav__item:not(:last-child) {
    margin-right: 33px; }

.nav__link {
  color: #f9fbfd;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px; }
  .nav__link:hover, .nav__link:focus {
    opacity: 0.7; }

.nav__logo {
  width: 70px;
  margin-right: 40px;
  width: 70px;
  height: 38px; }

@media (max-width: 1023px) {
  .nav__item:not(:last-child) {
    margin-right: 23px; }
  .nav__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px; }
  .nav__logo {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .nav__wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px; }
  .nav {
    flex-direction: column;
    align-items: flex-start; }
  .nav__logo {
    width: 42px;
    height: 22px; }
  .nav__link {
    color: #f9fbfd;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px; }
  .nav__toggle {
    position: relative;
    border: none;
    background-color: transparent;
    width: 35px;
    height: 30px;
    cursor: pointer;
    margin-right: -5px; }
  .nav--closed .nav__burger {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #f9fbfd;
    width: 70%;
    height: 2px;
    transition-timing-function: ease-in-out;
    transition-duration: 0.3s; }
  .nav--closed .nav__burger::before,
  .nav--closed .nav__burger::after {
    top: 0;
    left: 0;
    background-color: #f9fbfd;
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    transition-timing-function: ease-in-out;
    transition-duration: 0.3s; }
  .nav--closed .nav__burger::before {
    transform: translate(0, -8px); }
  .nav--closed .nav__burger::after {
    transform: translate(0, 8px); }
  .nav--opened .nav__burger {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    background-color: transparent;
    transform: translate(-50%, -50%);
    width: 70%;
    height: 2px;
    transition-timing-function: ease-in-out;
    transition-duration: 0.3s; }
    .nav--opened .nav__burger::before, .nav--opened .nav__burger::after {
      background-color: #f9fbfd;
      content: "";
      display: block;
      position: absolute;
      width: 24px;
      height: 2px;
      transition-timing-function: ease-in-out;
      transition-duration: 0.3s; }
    .nav--opened .nav__burger::before {
      transform: rotate(45deg); }
    .nav--opened .nav__burger::after {
      transform: rotate(-45deg); }
  .nav--closed .nav__list {
    display: none; }
  .nav--nojs .nav__toggle {
    display: none; }
  .nav--nojs .nav__list {
    display: block; }
  .nav__list {
    flex-direction: column; }
  .nav__item {
    align-items: flex-start; }
    .nav__item:not(:last-child) {
      margin-right: 0; } }

@media (min-width: 767px) {
  .nav__toggle {
    display: none; } }

.footer {
  flex: 0 0 auto;
  min-height: 138px;
  width: 100%;
  background-color: #011c40;
  padding: 50px 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px; }

.footer__list,
.footer__socials {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.footer__item {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 33px; }

.footer__link {
  color: #f9fbfd; }
  .footer__link:hover, .footer__link:focus {
    opacity: 0.7; }

.footer__icon:hover, .footer__icon:focus {
  opacity: 0.7; }

.footer__block {
  display: flex;
  align-items: center; }

.footer__list,
.footer__logo {
  margin-right: 7px; }

.footer__socials {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: auto;
  min-width: 150px; }

.footer__pict {
  width: 70px;
  height: 38px; }

.footer__fb {
  width: 11px;
  height: 20px; }

.footer__insta {
  width: 20px;
  height: 20px; }

.footer__tw {
  width: 20px;
  height: 16px; }

@media (max-width: 1023px) {
  .footer {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px; }
  .footer__list,
  .footer__logo {
    margin-right: 0; }
  .footer__socials {
    min-width: 110px; }
  .footer__pict {
    width: 38px;
    height: 20px; }
  .footer__item {
    margin-left: 23px; }
  .footer__fb {
    width: 9px;
    height: 16px; }
  .footer__insta {
    width: 16px;
    height: 16px; }
  .footer__tw {
    width: 16px;
    height: 13px; } }

@media (max-width: 767px) {
  .footer {
    padding: 40px 0; }
  .footer__block {
    flex-direction: column;
    align-items: flex-start; }
  .footer__list {
    flex-direction: column;
    margin-bottom: 10px; }
  .footer__socials {
    margin-left: 0; }
  .footer__item {
    margin-left: 0; }
    .footer__item--socials {
      margin-right: 23px; }
  .footer__logo {
    margin-bottom: 30px; }
  .footer__pict {
    width: 50px;
    height: 22px; } }
